import React from 'react';
import './App.css';
import { BrowserRouter , Route, Switch } from "react-router-dom";
import Navbar from './components/layout/Navbar'
import UserReg from './components/reg/UserReg'
import Register from './components/reg/Register'
import ResturantReg from './components/reg/ResturantReg'
import Resturants from './components/layout/Resturants'
import MyRequest from './components/layout/MyRequest'
import { Provider } from 'react-redux';
import store from './store';
import resAdmin from './components/layout/resAdmin'
import Details from './components/layout/Detail'
import Signup from './components/reg/Signup'
import Create from './components/reg/Create'

function App() {
  return (
      <Provider store={store}>
    <BrowserRouter>
    <div className="App">
      <Navbar/>
      <Switch>

      <Route exact path='/' component={Resturants}/>
      <Route  path='/user' component={UserReg}/>
      <Route  path='/register' component={Register}/>
      <Route  path='/resturant' component={ResturantReg}/>
      <Route  path='/myrequest' component={MyRequest}/>
      <Route  path='/adminresturant' component={resAdmin}/>
      <Route  path='/details' component={Details}/>
      <Route  path='/signup' component={Signup}/>
      <Route  path='/create' component={Create}/>
      </Switch>
     
    </div>
    </BrowserRouter>
    </Provider>
  );
}

export default App;
