import React, { Component } from 'react'
import Pending from './Pending'
import Progress from './Progress'
import Delievered from './Delievered'
import '../../App.css'

class resAdmin extends Component {
  constructor(){
      super();
      this.state={
          pending: true,
          progress: false,
          delieverd: false,
      }
  }
  
toggler(e){
    this.setState({
        pending: false,
        progress: false,
        delieverd: false,
        [e.target.id]: true,
    })
}
  
    render() {
        const {pending, progress, delieverd} = this.state
      return (
            <section className="container admin">
                <div className="row">
                    <div className="col s12 m4">
                    <div id="pending" className="btn waves-effect teal lighten-2 catbtn z-depth-0" onClick={this.toggler.bind(this)}>Pending</div>
                    </div>
                    <div className="col s12 m4">
                    <div id="progress" className="btn waves-effect blue lighten-2 catbtn z-depth-0" onClick={this.toggler.bind(this)}>progress</div>
                    </div>
                    <div className="col s12 m4">
                    <div id="delieverd" className="btn waves-effect green lighten-2 catbtn z-depth-0" onClick={this.toggler.bind(this)}>Delieverd</div>
                    </div>
                </div>

                <div className="row">
                {pending && <Pending/>}
                {progress && <Progress/>}
                {delieverd && <Delievered/>}
                </div>
            </section>
        )
    }
   
}


export default resAdmin