import React, { Component } from 'react'
import firebase from '../config/firebase'
import { connect } from 'react-redux';
import { getdata } from '../../store/actions/getData'

class MyRequest extends Component {
    constructor() {
        super();
        this.state = {
            email: '2'
        }
    }

    async componentDidMount() {
        var that = this
        firebase.auth().onAuthStateChanged(function (user) {
            if (user) {
                that.setState({
                    email: user.email
                })
                if(that.state.email !== "2"){
                    that.getMarkers()
                  }
            }
        })
        this.props.send_data();
     

    }


    async getMarkers() {
        const markers = [];
        const { email } = this.state
        await firebase.firestore().collection('users').doc(email).collection("order").get()
            .then(querySnapshot => {
                querySnapshot.docs.forEach(doc => {
                    markers.push(doc.data());
                    this.setState({
                        markers
                    })
                });
                console.log(this.state)
            });
        return markers;




    }

    render() {
        let email = ''
        let name = ''
        if (this.props.email.todos.length > 0) {
            email = this.props.email.todos[0].email
            name = this.props.email.todos[1].first
            // console.log(name) 
        }
        const  {markers} = this.state
        return (
            <div className="container">
                <div className="row">
                    <div className="col s6">
                        name: {name && name}
                    </div>
                    <div className="col s6">
                        email: {email && email}
                    </div>
                </div>
                <section>
                    <table className="striped">
                        <thead>
                            <tr>
                                <th>Status</th>
                                <th>Item Name</th>
                                <th>Item Price</th>
                            </tr>
                        </thead>

                        <tbody>
                            {markers && markers.map((items)=>{
                                return(
                            <tr key={items.name}>
                                <td>{items.status}</td>
                                <td>{items.name}</td>
                                <td>{items.price}</td>
                            </tr>
                                )
                            })}
                        </tbody>
                    </table>
                </section>
            </div>
        )
    }
}


const mapStateToProps = state => {
    return {
        email: state.user,
    }

}

const mapDispatchToProps = dispatch => {
    return {
        send_data: () => dispatch(getdata())
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(MyRequest)