import React, { Component } from 'react'
import Chinese from '../cat/Chinese'
import FastFood from '../cat/FastFood'
import Bbq from '../cat/Bbq'
import Deserts from '../cat/Deserts'
import '../../App.css'

class MainBody extends Component {
    constructor(props){
        super(props);
        this.state={
            chinese: true,
            fastfood: true,
            bbq: true,
            deserts: true
        }
    }
 
toggler(e){
    
    this.setState({
        chinese: false,
        fastfood: false,
        bbq: false,
        deserts: false,
        [e.target.id]: true,
    })
}

    render() {
        const {chinese, fastfood, bbq, deserts} = this.state
        
        return (
            <div>
                <div className="container left">
                <div className="row">
                    <div className="col s6 m3 l3 catbtn">

                        <div id="chinese" className="btn waves-effect orange lighten-2 catbtn z-depth-0" onClick={this.toggler.bind(this)}>Chinese</div>

                    </div>
                    <div className="col s6 m3 l3 catbtn">

                        <div id="fastfood" className="btn waves-effect red lighten-2 catbtn z-depth-0" onClick={this.toggler.bind(this)}>Fast Food</div>

                    </div>
                    <div className="col s6 m3 l3 catbtn">

                        <div id="bbq" className="btn waves-effect brown lighten-2 catbtn z-depth-0" onClick={this.toggler.bind(this)}>BBq</div>

                    </div>
                    <div className="col s6 m3 l3 catbtn">

                        <div id="deserts" className="btn waves-effect pink lighten-2 catbtn z-depth-0" onClick={this.toggler.bind(this)}>Deserts</div>

                    </div>
                     </div>
                </div >


                {chinese && <Chinese/>}
                {fastfood && <FastFood/>}
                {bbq && <Bbq/>}
                {deserts && <Deserts/>}

            </div>
        )
    }
}

export default MainBody;