import React, { Component } from 'react'
import firebase from 'firebase';

class Create extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            category: '',
            price: '',
            decription: ''
        }

        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }

    handleChange(e) {
        this.setState({
            [e.target.id]: e.target.value,
        })
    }

    handleSubmit() {
        var that = this
        const { name, category, price, decription } = this.state;
        var db = firebase.firestore()

        if (category) {

            firebase.auth().onAuthStateChanged(function (user) {
                if (user) {
                    db.collection(category).doc(name).set({
                        name: name,
                        category: category,
                        price: price,
                        decription: decription,
                        email: user.email,
                        id: new Date()

                    })
                        .then(function (docRef) {

                        })
                        .catch(function (error) {
                            console.error("Error adding document: ", error);
                        });


                    // User is signed in.
                    var email = user.email;
                    db.collection("resturants").doc(email).collection("item").add({
                        name: name,
                        category: category,
                        price: price,
                        decription: decription,
                        email: user.email
                    })
                        .then(function (docRef) {
                            alert("sucess")
                            that.porps.history.push('/')
                        })
                        .catch(function (error) {
                            console.error("Error adding document: ", error);
                        });

                    // ...
                } else {
                    // User is signed out.
                    // ...
                }
            });



        }
    }
    render() {
        return (
            <div className="container">
                <div className="section">
                    <div className="card-panel">
                        <h4 className="header2">Form with placeholder</h4>
                        <div className="row">
                            <div className="col s12">
                                <div className="row">
                                    <div className="input-field col s12">
                                        <input placeholder="Tikka" onChange={this.handleChange} id="name" type="text" />
                                        <label htmlFor="name" className="active">Name</label>
                                    </div>
                                    <div className="input-field col s12">
                                        <input placeholder="category" onChange={this.handleChange} id="category" type="text" />
                                        <label htmlFor="category" className="active">Category</label>
                                    </div>
                                    <div className="input-field col s12">
                                        <input placeholder="price" onChange={this.handleChange} id="price" type="text" />
                                        <label htmlFor="price" className="active">Price</label>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="input-field col s12">
                                        <textarea placeholder="Oh WoW! Let me check this one too." id="decription" onChange={this.handleChange} className="materialize-textarea"></textarea>
                                        <label htmlFor="decription" className="active">Decription</label>
                                    </div>
                                    <div className="row">
                                        <div className="input-field col s12">
                                            <button className="btn cyan waves-effect waves-light right" onClick={this.handleSubmit} type="submit">Submit
                                <i className="mdi-content-send right"></i>
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


export default Create;