import firebase from '../../components/config/firebase'

const db = firebase.firestore();


const signup =(state = {}, action)=>{
    switch(action.type){
    case "SET_SIGNUP":{
           
        let first = action.data.lastName;
        let last = action.data.age;
        let age = action.data.lastName;
        let email = action.data.email;
        let country = action.data.country;
        let city = action.data.city;
        db.collection("users").doc(email).set({
            first: first,
            last: last,
            age: age,
            email: email,
            country: country,
            city: city
        })
        .then(function(docRef) {
            alert("sucess")
        })
        .catch(function(error) {
            console.error("Error adding document: ", error);
        });
     return state;
    }
    default:{
        return state
    }
}

}

export{
    signup
}