import React, { Component } from 'react'
import cover from '../../assets/image/cover.jpg'
import '../../App.css'
import DetailsItemCard from './DetailsItemCard'
import Reviews from './Reviews';
import firebase from '../config/firebase'

class Detail extends Component {
    constructor(){
        super();
        this.state= {
            email: 2,
            data: ''
        }
    }
componentWillMount(){
    this.setState({
        data: this.props.location.state.data,
        email: this.props.location.state.data.email,
    })

}

    componentDidMount(){
        if(this.state.email !== 2){
            this.getMarkers()
            
         }
    }

    async getMarkers() {
        const {email} =this.state
        const markers = [];
        await firebase.firestore().collection('resturants').doc(email).collection("item").get()
          .then(querySnapshot => {
            querySnapshot.docs.forEach(doc => {
            markers.push(doc.data());
            this.setState({
                markers
            })
        });
        });
        return markers;
      }
    render() {
        const {markers} = this.state
        // console.log(this.state.data)
        return (
            <div className="container section">
                <div className="row">
                <div className="col s12">
                    <img className="cover-img" src={cover} alt={cover}/>
                </div>
                </div>
                <div className="row">
                    <div className="col s12">
                        <h2 className="flow-text">Hotel</h2>
                        <hr/>
                    </div>
                </div>
                <div className="row">
                    <div className="col s6 m3 ">
                        <div className="btn-large">Cat</div>
                    </div>
                    <div className="col s6 m3 ">
                        <div className="btn-large">Cat</div>
                    </div>
                    <div className="col s6 m3 ">
                        <div className="btn-large">Cat</div>
                    </div>
                    <div className="col s6 m3 ">
                        <div className="btn-large">Cat</div>
                    </div>
                </div>
                <div className="row">
                    <div className="col s6 m4 l3">
                        {markers && markers.map((item)=>{
                            return(
                                <DetailsItemCard key={item.price} tittle={item.name} price={item.price} content={this.content}/>
                            )
                        })}
                    </div>
                </div>
                <Reviews/>
            </div>
        )
    }
}
 
export default Detail;