import React, { Component } from 'react'
import firebase from '../config/firebase'
import {connect} from 'react-redux';
import {isSignUP} from '../../store/actions/signup'

class Signup extends Component {
    constructor(props){
        super(props);
        this.state={
            firstName: '',
            lastName: '',
            gender: '',
            city: '',
            age: '',
            email: '',
            password: '',
            country: '',
            login: true
        }
    }
handleSubmit=(e)=>{
    this.setState({
     [e.target.id]: e.target.value
    })
    
}

handlClick(e){
    const {email, password} = this.state;

    firebase.auth().createUserWithEmailAndPassword(email, password).catch(function(error) {
        // Handle Errors here.
        var errorMessage = error.message;
        alert(errorMessage)
        // ...  
    })
   
this.props.send(this.state)
}


   

    render() {

        return (
            <div className="container section">
                <div className="row">
                    <div className="col m3"></div>
                    <div className='col s6 m4'>
                        <div className="input-field">
                            <input type="text" id="firstName" onChange={this.handleSubmit} placeholder="first name"/>
                        </div>
                    </div>
                    <div className='col s6 m4'>
                        <div className="input-field">
                            <input type="text" id="lastName" onChange={this.handleSubmit} placeholder="last name"/>
                        </div>
                    </div>
                </div>
                
                <div className="row">
                    <div className="col m3"></div>
                    <div className='col s6 m4'>
                        <div className="input-field">
                            <input type="text" id="gender" onChange={this.handleSubmit} placeholder="gender"/>
                        </div>
                    </div>
                    <div className='col s6 m4'>
                        <div className="input-field">
                            <input type="text" id="city" onChange={this.handleSubmit} placeholder="city"/>
                        </div>
                    </div>
                </div>
                
                <div className="row">
                    <div className="col m3"></div>
                    <div className='col s6 m4'>
                        <div className="input-field">
                            <input type="text" id="age" onChange={this.handleSubmit} placeholder="age"/>
                        </div>
                    </div>
                    <div className='col s6 m4'>
                        <div className="input-field">
                            <input type="text" id="country" onChange={this.handleSubmit} placeholder="country"/>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col m3"></div>
                    <div className="col s12 m8">
                        <div className="input-field">
                            <input type="email" id="email" onChange={this.handleSubmit} placeholder="email"/>
                        </div>
                    </div>
                </div>
                <div className="row">
                <div className="col m3"></div>
                    <div className="col s12 m8">
                        <div className="input-field">
                            <input type="password" id="password" onChange={this.handleSubmit} placeholder="password"/>
                        </div>
                    </div>
                </div>

                <div className="row">
                <div className="col s3"></div>
                <div className="col s8 push-2">
                    <div onClick={()=>{this.handlClick()}} className="input-field btn">
                        Sign up
                    </div>
                </div>
                </div>
            </div>
        )
    }
}

const mapDispatchToProps = dispatch => {
    return{
        send: (loginData) => dispatch(isSignUP(loginData))
    }
}

export default connect(null, mapDispatchToProps)(Signup);