import React, { Component } from 'react'
import Search from '../others/Search'
import '../../App.css';
import MainBody from './MainBody';
import mainCover from '../../assets/image/main-cover.jpg';

class Resturants extends Component {
    render() {
        return (
            
            <div>
                <div className="row">
                    <div className="col s12 z-depth-0 ">
                        <img src={'mainCover'} className="main-cover" alt={mainCover}/>
                    </div>
                </div>
                <div className="container">
                <div className="row">
                    <div className="col s12">
                        <Search />
                    </div>
                </div>
                <div className="row">
                    <div className="col s12 offset-m2">
                <MainBody toggler={this.state} />
                    </div>
                </div>
                </div>
            </div>

        )
    }
}


export default Resturants;
