import React, { Component } from 'react'
import FoodItem from '../cat/Fooditem'
import {connect} from 'react-redux'
import {menuItem} from '../../store/actions/itemAction'
import firebase from '../config/firebase'

class Chinese extends Component {
constructor(){
    super();
    this.state= {
        markers: []
    }
}
    async componentDidMount() {
        this.props.send_data();
        this.getMarkers()
    }


    async getMarkers() {
        const markers = [];
        await firebase.firestore().collection('chinese').get()
          .then(querySnapshot => {
            querySnapshot.docs.forEach(doc => {
            markers.push(doc.data());
            this.setState({
                markers
            })
          });
        });
        return markers;
      }
    render() {
        const markers = this.state

        
        return (
            
            <div>
                {markers && markers.markers.map((item)=>{
                    return <FoodItem  email={item.email} price={item.price} header={item.name} key={item.name} content={item.decription}/>
                })}
            </div>
          
        )
    }
}


const mapStateToProps = (state) => {
    return{
        items: state
    }       
}


const mapDispatchToProps = dispatch => {
    return {
        send_data: () => dispatch(menuItem())
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Chinese)

