import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import {connect} from 'react-redux'
import {islogin} from '../../store/actions/login'

class UserReg extends Component {
        constructor(){
        super();
        this.state={
        }
    }

    handleChange=(e)=>{
        this.setState({
            [e.target.id]: e.target.value
        })
    }
    
    handleSubmit(){
        this.props.send(this.state)
        this.props.history.push('/')
    }

    render() {
        return (
          <div className="section container">
            <h4>Login</h4>
            <div className="row">
                <div className="col s4"></div>
                <div className="col s4">
                    <div className="input-field">
                        <input type="email" id="email" onChange={this.handleChange}  placeholder="EMAIL..." />
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col s4"></div>
                <div className="col s4">
                    <div className="input-field">
                        <input type="password" id="password" onChange={this.handleChange} placeholder="PASSWORD..."/>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col s4"></div>
                <div className="col s4">
                    <div className="input-field">
                       <div className="input-field btn" onClick={this.handleSubmit.bind(this)}>LOGIN</div>
                    </div>
                     <Link to='/signup'>  <div className="input-field btn-small">Signup</div></Link>
                </div>
            </div>
          </div>
        )
    }
}

const mapDispatchToProps = dispatch =>{
    return {
        send:(loginData) => dispatch(islogin(loginData))
    }
}

export default connect(null,mapDispatchToProps)(UserReg);