import React, { Component } from 'react'

class Delievered extends Component {
    render() {
        return (
            <div>
                <table className="striped">
                    <thead>
                        <tr>
                            <th>Status</th>
                            <th>Item Name</th>
                            <th>Item Price</th>
                            <th>Set Status to Delievered</th>
                        </tr>
                    </thead>

                    <tbody>
                        <tr>
                            <td>Delievered</td>
                            <td>Eclair</td>
                            <td>$0.87</td>
                            <td className="center"><div className="btn-floating waves-effect waves-light red"><i className="material-icons">add</i></div></td>
                        </tr>
                    </tbody>
                </table>
                <div className="row">
                    <div className="col s10 push-m6">
                        <div className="btn green darken-1">Delievered</div>
                    </div>
                </div>
            </div>
        )
    }
}


export default Delievered