import React, { Component } from 'react'
import firebase from 'firebase';

 class ResturantReg extends Component {
  
  constructor(){
    super();
    this.state={
      email: '',
      password: '',
      ownerName: '',
      resturantName: ''
    }

    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }
  
  handleChange(e){
    this.setState({
      [e.target.id]: e.target.value,
    })
    }

  handleSubmit(){
    const {email, password, ownerName, resturantName} = this.state;

    firebase.auth().createUserWithEmailAndPassword(email, password).catch(function(error) {
        // Handle Errors here.
        var errorMessage = error.message;
        alert(errorMessage)
        // ...  
    })
    var db = firebase.firestore()
    if(email){

      db.collection("resturants").doc(email).set({
        ownerName: ownerName,
        resturantName: resturantName,
        email: email,
  })
  .then(function(docRef) {
      alert("sucess")
      this.props.history.push('/create')
  })
  .catch(function(error) {
    console.error("Error adding document: ", error);
  });

  
}
}

  render() {
        return (
            <div className="container">
            <div className="card-panel">
            <h4 className="header2">Resturant Sign up</h4>
            <div className="row">
              <div className="col s12">
                <div className="row">
                  <div className="input-field col s12">
                    <i className="mdi-action-account-circle prefix"></i>
                    <input id="ownerName" onChange={this.handleChange} type="text" className="validate"/>
                    <label forhtml="first_name">Name</label>
                  </div>
                </div>
                <div className="row">
                  <div className="input-field col s12">
                    <i className="mdi-communication-email prefix"></i>
                    <input id="email" onChange={this.handleChange} type="email" className="validate"/>
                    <label forhtml="email">Email</label>
                  </div>
                </div>
                <div className="row">
                  <div className="input-field col s12">
                    <i className="mdi-action-lock-outline prefix"></i>
                    <input id="password" onChange={this.handleChange} type="password" className="validate"/>
                    <label forhtml="password">Password</label>
                  </div>
                </div>
                <div className="row">
                  <div className="input-field col s12">
                    <i className="mdi-action-question-answer prefix"></i>
                    <textarea id="resturantName" onChange={this.handleChange} className="materialize-textarea validate" length="120"></textarea>
                    <label forhtml="ResturantName">Resturant Name</label>
                  <span className="character-counter" ></span></div>
                  <div className="row">
                    <div className="input-field col s12">
                      <button className="btn cyan waves-effect waves-light right" onClick={this.handleSubmit}>Submit
                        <i className="mdi-content-send right"></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          </div>
        )
    }
}

export default ResturantReg;