import React, { Component } from 'react'
import biryani from '../../assets/image/biryani.gif'
import { Link } from 'react-router-dom'
import firebase from '../config/firebase'

class Fooditem extends Component {


 
    
    handleClick(e) {
        // console.log(this.props)
        var {header,price, email}= this.props
        var db = firebase.firestore()
        firebase.auth().onAuthStateChanged(function (user) {
            if (user) {
                // User is signed in.
                var emails = user.email;
                var docRef = db.collection('users').doc(emails).collection("order");

                docRef.add({
                    name: header,
                    price,
                    email,
                    status: "pending"
                });
                var docref = db.collection('resturants').doc(email).collection("sell");

                docref.add({
                    name: header,
                    price,
                    emails,
                    orderStatus: "pending"
                });
                // ...
            } else {
                // User is signed out.
                // ...
            }
        });
    }

    render() {
        return (
            <div>
                <div className="row" id={this.props.email}>
                    <div className="col s12 m10 pull-m2">
                        <h5 className="header">{this.props.header}</h5>
                        <div className="card horizontal">
                            <Link to={{
                                pathname: '/details',
                                state: {
                                   data: this.props
                                }
                            }}>
                                <div className="card-image">
                                    <img src={biryani} alt={biryani} />
                                </div>
                            </Link>
                            <div className="card-stacked">
                                <div className="card-content">
                                    <p>{this.props.content}</p>
                                </div>
                                <div className="card-action">
                                    <div onClick={this.handleClick.bind(this)} className="btn btn-floating green"><i className="material-icons">add</i></div>
                                    <p>Price Rs {this.props.price}</p>
                                    <p>Ratings</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}



export default Fooditem