import React, { Component } from 'react'

class Pending extends Component {
    render() {
        return (
            <div>
                <table className="striped">
                    <thead>
                        <tr>
                            <th>Status</th>
                            <th>Item Name</th>
                            <th>Item Price</th>
                            <th>Set to progress</th>
                        </tr>
                    </thead>

                    <tbody>
                        <tr>
                            <td>Pending</td>
                            <td>Eclair</td>
                            <td>$0.87</td>
                            <td className="center"><div className="btn-floating waves-effect waves-light red"><i className="material-icons">add</i></div></td>
                        </tr>
                    </tbody>
                </table>
            </div>
        )
    }
}


export default Pending