import React, { Component } from 'react'
import { Link } from "react-router-dom";
import {NavLink} from 'react-router-dom';
import { connect } from 'react-redux';
import SignOut from '../reg/SignOut'
import firebase from '../config/firebase'
 class Navbar extends Component {
    constructor(props) {
        super(props);
        this.handleLoginClick = this.handleLoginClick.bind(this);
        this.handleLogoutClick = this.handleLogoutClick.bind(this);
        this.state = {
          isLoggedIn: false,
          user: false,
          admin: false,
          logger: false
        };
      }
      handleLoginClick() {
        this.setState({isLoggedIn: true});
        
      }
    
      handleLogoutClick() {
        firebase.auth().signOut().then(function() {
          // Sign-out successful.
        }).catch(function(error) {
          // An error happened.
        });
        this.setState({isLoggedIn: false,
                        user: false,
                        admin: false,
                        logger: false
        });
      }
  
   async componentWillMount(){
        var that = this
        // console.log(this.state)
        firebase.auth().onAuthStateChanged(function(user) {
          if (user) {
            var db =firebase.firestore();
            var docRef = db.collection("resturants").doc(user.email);
            docRef.get().then(function(doc) {
              if (doc.exists) {
                
                // console.log("1st")
                that.setState({
                  admin: true
                })
                  // console.log("Document data:", doc.data());
              } else {
                var docref = db.collection("users").doc(user.email) 
                docref.get().then(function(doc) {
                  if (doc.exists) {
                    that.setState({
                        user: true
                      })
                  } else {
                      // doc.data() will be undefined in this case
                      console.log("No such document!");
                  }
              }).catch(function(error) {
                  console.log("Error getting document:", error);
              });
              }
          }).catch(function(error) {
              console.log("Error getting document:", error);
          });
            
          }
        }).bind(this)
      }
      
      render() {
        // console.log(this.state)
        let { admin, user, logger } = this.state;
        let link;
        if(admin === true){
          link = "/create"
        }else{
          link = "/myrequest"
        }
      
    let button;

    if (admin || user) {
      button = <span onClick={this.handleLogoutClick}><SignOut /></span>
    } else {
      button = <span onClick={this.handleLoginClick}><NavLink  to='/register'>Login</NavLink></span>;
    }
 
   if(admin || user){
     logger = true
   }
        return (
                <nav className="">
                    <div className="nav-wrapper teal">
                        <Link to="/" className="brand-logo white-text">Hotel</Link>
                        <ul id="mobile-nav" className="right hide-on-med-and-down">
                           {/* {!isLoggedIn && <li className="lisofnav"><NavLink to='/adminresturant'>Admin</NavLink></li>} */}
                           {logger && <li className="lisofnav" ><NavLink to={link}><div className="btn-floating">P</div></NavLink></li>}
                           {<li className="lisofnav">{button}</li>}
                           </ul>
                    </div>
                </nav>
            )
    }
}

const mapStateToProps = state =>{
    return{
        login: state.loginData,
        getDAta: state
    }
}


export default connect(mapStateToProps)(Navbar)
